.wrapper-blue {
    background: url('../img/bgblue.jpg');
    background-repeat: no-repeat;
    background-size: cover;

    .logo-letter {
        color: #24c1d6;
    }

    .checkbox-tools:checked + label {
        box-shadow: 0px 0px 15px 4px #24C1D6, 0px 4px 32px #210857;

        .label-title {
            background: #E7DEFC;
        }

        .label-summ {
            color: #24C1D6;
        }
    }
}


.btn-blue {
    background: linear-gradient(286.16deg, #12BCD2 5.83%, #DCF3FF 117.26%);

    &:hover {
        background: linear-gradient(140.16deg, #12BCD2 5.83%, #DCF3FF 117.26%);
    }
}
